import React, {useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import {bindActionCreators, compose} from "redux";
import {connect} from "react-redux";
import SessionByDevice from "../../view/SessionByDevice";
import SummaryAction from '../../redux/action/summaryAction'
import Grid from '@material-ui/core/Grid';
import FusionChart from '../../view/reactFusionChart'
import signStatAction from '../../redux/action/signStatAction'
import CircularProgress from "@material-ui/core/CircularProgress";
import ApexChart from '../../view/chart'


const useStyles = makeStyles({
    table: {
        minWidth: 300,
    },
});

let $info = "#02FAD9",
    $potential = "#0349FC",
    $low = "#EBFB04",
    $medium = "#03FA14",
    $high = "#FC6202",
    $critical = "#FE0606",
    $info_light = "#81F4E5",
    $potential_light = "#678EF3",
    $low_light = "#EFF876",
    $medium_light = "#82F58A",
    $high_light = "#FCA975",
    $critical_light = "#FD8585"


function SummaryPage(props) {

    useEffect(() => {
        props.summaryAction.getSummary()
    }, []);

    return (
        <Grid container>
            <Grid item xs={6}>
                <SessionByDevice
                    series={props.dataApi?props.dataApi:null}
                    Info={$info}
                    Potential={$potential}
                    Low={$low}
                    Medium={$medium}
                    High={$high}
                    Critical={$critical}
                    InfoLight={$info_light}
                    PotentialLight={$potential_light}
                    LowLight={$low_light}
                    MediumLight={$medium_light}
                    HighLight={$high_light}
                    CriticalLight={$critical_light}
                />
            </Grid>
            <Grid item xs={6}>
                <ApexChart/>
            </Grid>
        </Grid>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        summaryAction: bindActionCreators(SummaryAction, dispatch),
        signStatAction:bindActionCreators(signStatAction,dispatch)
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        loading: state.SummaryReducer.loading,
        dataApi: state.SummaryReducer.data,
        dataColum:state.SignStatReducer.data,
        categories:state.SignStatReducer.categories
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
)

export default compose(withConnect)(SummaryPage);