import React from "react"
import {Card, CardBody, CardHeader, CardTitle} from "reactstrap"
import Chart from "react-apexcharts"
import Box from '@material-ui/core/Box';
import Grid from "@material-ui/core/Grid";

class SessionByDevice extends React.Component {


    componentWillReceiveProps(nextProps, nextContext) {
        if(nextProps.series !== this.props.series){
            this.setState({...this.state,series:nextProps.series})
        }
    }

    state = {
        options: {
            chart: {
                toolbar: {
                    show: false
                }
            },
            dataLabels: {
                enabled: false
            },
            legend: {show: false},
            // comparedResult: [2, -3, 8],
            labels: ["Info", "Potential", "Low", "Medium", "High", "Critical"],
            stroke: {width: 0},
            colors: [this.props.Info, this.props.Potential, this.props.Low, this.props.Medium, this.props.High, this.props.Critical],
            fill: {
                type: "gradient",
                gradient: {
                    gradientToColors: [
                        this.props.InfoLight,
                        this.props.PotentialLight,
                        this.props.LowLight,
                        this.props.MediumLight,
                        this.props.HighLight,
                        this.props.CriticalLight
                    ]
                }
            }
        },
        series: this.props.series?this.props.series:[0,0,0,0,0,0]
    }

    render() {
        return (
            <Card>
                <CardHeader>
                    <CardTitle>Vulnerabitity Summary</CardTitle>
                    {/*<UncontrolledDropdown>*/}
                    {/*    <DropdownToggle tag="small" className="text-bold-500 cursor-pointer">*/}
                    {/*        Last 7 days <ChevronDown size={10}/>*/}
                    {/*    </DropdownToggle>*/}
                    {/*    <DropdownMenu right>*/}
                    {/*        <DropdownItem>Last 28 days</DropdownItem>*/}
                    {/*        <DropdownItem>Last Month</DropdownItem>*/}
                    {/*        <DropdownItem>Last Year</DropdownItem>*/}
                    {/*    </DropdownMenu>*/}
                    {/*</UncontrolledDropdown>*/}
                </CardHeader>
                <CardBody className="pt-0">
                    <Chart
                        options={this.state.options}
                        series={this.state.series}
                        type="donut"
                        height={500}
                    />
                    <Grid container spacing={3}>
                        {this.state.options.labels ? this.state.options.labels.map((label, index) => (
                            <Grid item xs={2}>
                                <span className="text-bold-600 mx-50">{label}</span>
                                <span className="align-middle"> -{this.props.series?this.props.series[index]:null}</span>
                                <Box p={2}
                                     bgcolor={
                                         label === "Info" ? this.props.Info :
                                         label === "Potential" ? this.props.Potential :
                                         label === "Low" ? this.props.Low :
                                         label === "Medium" ? this.props.Medium :
                                         label === "High" ? this.props.High :
                                         label === "Critical" ? this.props.Critical : null
                                     }
                                     color=
                                         {
                                             label === "Info" ? this.props.Info :
                                             label === "Potential" ? this.props.Potential :
                                             label === "Low" ? this.props.Low :
                                             label === "Medium" ? this.props.Medium :
                                             label === "High" ? this.props.High :
                                             label === "Critical" ? this.props.Critical : null
                                         }
                                ></Box>
                            </Grid>
                        )) : ""}
                    </Grid>
                </CardBody>
            </Card>
        )
    }
}

export default SessionByDevice
