import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import signaturesAction from "../../action/signaturesAction";
import * as Api from '../../api/signaturesApi'


function * getSignaturesAction({payload}) {
    let resp = yield call(Api.getSignatures,"api/signatures");
    const {status, signatures} = resp;
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(signaturesAction.getSignaturesSuccess(signatures));
    }else{
        yield put(signaturesAction.getSignaturesFaild());
    }
}



function* getSignaturesSaga() {
    yield takeLatest(config.GET_SIGNATURE_ACTION,getSignaturesAction)
}

export default getSignaturesSaga;