import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import summaryAction from "../../action/summaryAction";
import * as Api from '../../api/summaryApi'


function * getSummaryAction({payload}) {
    let resp = yield call(Api.getSummary,"api/stats/vuln");
    const {status,stats} = resp;
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(summaryAction.getSummarySuccess(stats));
    }else{
        yield put(summaryAction.getSummaryFaild());
    }
}



function* getSummarySaga() {
    yield takeLatest(config.GET_SUMMARY_ACTION,getSummaryAction)
}

export default getSummarySaga;