import * as config from '../../../constant/config'
const initial = {
    loading:false
}

const SignaturesReducer = (state = initial, action) => {
    switch (action.type) {
        case config.GET_SIGNATURE_ACTION: {
            return {...state,loading: true}
        }
        case config.GET_SIGNATURE_SUCCESS:{
            return {...state,loading:false,data:action.data}
        }
        case config.GET_SIGNATURE_FAILED:{
            return {...state,loading: false}
        }
        default:
            return state;
    }
}

export default SignaturesReducer;