import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import addScanAction from "../../action/addScanAction";
import * as Api from '../../api/addScanApi'


function * addScanActions({payload}) {
    debugger
    var {scan} = payload
    let resp = yield call(Api.addScan,"api/addScan",scan);
    const { status, records} = resp;
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(addScanAction.addScanSuccess());
    }else{
        yield put(addScanAction.addScanFaild());
    }
}



function* addScanSaga() {
    yield takeLatest(config.ADD_SCAN_ACTION,addScanActions)
}

export default addScanSaga;