import React, {useEffect, useLayoutEffect} from 'react';
import {withStyles, makeStyles} from '@material-ui/core/styles';
import {connect} from "react-redux";
import {bindActionCreators, compose} from "redux";
import scanActions from "../../redux/action/scanAction/scanAction";
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import scanDetailActions from "../../redux/action/scanDetailAction";
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Chip from '@material-ui/core/Chip';
import {CodeSnippet} from 'carbon-components-react';
import copy from 'clipboard-copy';
import {
    withKnobs,
    boolean,
    text,
    select,
    number,
} from '@storybook/addon-knobs';
import {action} from '@storybook/addon-actions';
import CircularProgress from "@material-ui/core/CircularProgress";


const useStyles = makeStyles({
    paper: {
        padding: 2,
        textAlign: 'left',
    },
});


function CustomizedTables(props) {
    const classes = useStyles();
    useLayoutEffect(() => {
        props.scanDetailActions.getScandetail(props.id)
    }, []);

    const getScanDetail = (ScanID) => {
        props.scanDetailActions.getScandetail(ScanID)
    }
    const propss = () => ({
        type: select(
            'Type (type)',
            {
                inline: 'inline',
                'single line': 'single',
                'multiple line': 'multi',
            },
            'inline'
        ),
        disabled: boolean('Disabled (disabled)', false),
        light: boolean('Light variant (light)', false),
        feedback: text('Feedback text (feedback)', 'Copied to clipboard'),
        feedbackTimeout: number('Feedback text timout (feedbackTimeout)', 2000),
        showMoreText: text('Text for "show more" button', 'Show more'),
        showLessText: text('Text for "show less" button', 'Show less'),
        hideCopyButton: boolean('Hide copy button (hideCopyButton)', false),
        onClick: action('onClick'),
        copyButtonDescription: text('Copy button title', 'Copy code snippet'),
        ariaLabel: text('ARIA label', 'Container label'),
        wrapText: boolean('Wrap text (wrapText)', true),
        maxCollapsedNumberOfRows: number(
            'maxCollapsedNumberOfRows: Specify the maximum number of rows to be shown when in collapsed view',
            15
        ),
        maxExpandedNumberOfRows: number(
            'maxExpandedNumberOfRows: Specify the maximum number of rows to be shown when in expanded view',
            0
        ),
    });
    return (
        <>
            <h1>List Records</h1>
            {props.dataAPI ?
                props.dataAPI.map((row) => {
                    return (
                        <div>
                            <h2>Record</h2>
                            <Grid container spacing={3}>
                                <Grid item xs={6} sm={6}>
                                    <Paper style={{textAlign: 'center'}} className={classes.paper}>Issues:
                                        <Chip className={classes.chip} style={{backgroundColor: "#90EE90"}}
                                              label={row.Issues}/>
                                    </Paper>
                                </Grid>
                                <Grid item xs={6} sm={6}>
                                    <Paper style={{textAlign: 'center'}}
                                           className={classes.paper}>Risk:
                                        <Chip className={classes.chip} style={{backgroundColor: "#CD5C5C"}}
                                              label={row.Risk}/>
                                    </Paper>
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <Paper className={classes.paper}>
                                        <h3 style={{textAlign: "left"}}>Request</h3>
                                        {row.ReqRaw}
                                    </Paper>
                                </Grid>
                                <Grid item xs={12} sm={12}>
                                    <Paper className={classes.paper}>
                                        <Grid item xs={12}>
                                            <h4 style={{textAlign: 'left', paddingBottom: '5px'}}>Response</h4>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <CodeSnippet {...propss()} type="multi" feedback="Copied to clipboard">
                                                {row.ResRaw}
                                            </CodeSnippet>
                                        </Grid>
                                    </Paper>
                                </Grid>
                            </Grid>

                            <br/>
                            <br/>
                        </div>
                    )
                }) : null
            }
            {
                props.loading ?
                    <CircularProgress color="secondary"/>
                    : null
            }
        </>
    );
}


const mapDispatchToProps = dispatch => {
    return {
        scanActions: () => dispatch(scanActions.getScan()),
        scanDetailActions: bindActionCreators(scanDetailActions, dispatch)
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        dataAPI: state.ScandetailReducer.data,
        loading: state.ScandetailReducer.loading,
        id: state.ScandetailReducer.id
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps,
)

export default compose(withConnect)(CustomizedTables);