import React, {Component} from 'react';
import {withStyles} from "@material-ui/styles";
import ProtoTypes from 'prop-types';
import styles from './styles';
import {
    Collapse,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Nav,
    Navbar,
    NavbarBrand,
    NavbarToggler,
    NavItem,
    NavLink,
    UncontrolledDropdown
} from 'reactstrap';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';



const options = [
    'Login',
];


class Header extends Component {

    constructor() {
        super();
        this.state = {
            value: 0,
            dropDownOpen: false,
            iconMenuOpen: null
        }
    }

    toggle = () => {
        this.setState({...this.state, dropDownOpen: !this.state.dropDownOpen})
    }

    handleClick = (event) => {
        this.setState({...this.state,iconMenuOpen:event.currentTarget});
    };

    handleClose = () => {
        this.setState({...this.state,iconMenuOpen:null});
    };


    render() {
        const {classes} = this.props
        let open = Boolean(this.state.iconMenuOpen)
        return (
            <div className={classes.root}>
                <Navbar color="dark" light expand="md">
                    <NavbarBrand style={{color:"white"}} href="/"><h3>GoSc4n</h3></NavbarBrand>
                    <NavbarToggler onClick={this.toggle}/>
                    <Collapse isOpen={this.state.dropDownOpen} navbar>
                        <Nav  className="mr-auto" pills>
                            <NavItem>
                                <NavLink href="/summary">Summary</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink href="/scans">Scans</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink href="/signatures">Signatures</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink href="/teamDetail">About Us</NavLink>
                            </NavItem>
                            {/*<UncontrolledDropdown nav inNavbar>*/}
                            {/*    <DropdownToggle nav caret>*/}
                            {/*        Controls*/}
                            {/*    </DropdownToggle>*/}
                            {/*    <DropdownMenu right>*/}
                            {/*        <DropdownItem>*/}
                            {/*            Option 1*/}
                            {/*        </DropdownItem>*/}
                            {/*        <DropdownItem>*/}
                            {/*            Option 2*/}
                            {/*        </DropdownItem>*/}
                            {/*        <DropdownItem divider/>*/}
                            {/*        <DropdownItem>*/}
                            {/*            Reset*/}
                            {/*        </DropdownItem>*/}
                            {/*    </DropdownMenu>*/}
                            {/*</UncontrolledDropdown>*/}
                        </Nav>
                        <IconButton
                            style={{color:"white"}}
                            aria-label="more"
                            aria-controls="long-menu"
                            aria-haspopup="true"
                            onClick={this.handleClick}
                        >
                            <MoreVertIcon/>
                        </IconButton>
                        <Menu
                            id="long-menu"
                            anchorEl={this.state.iconMenuOpen}
                            keepMounted
                            open={open}
                            onClose={this.handleClose}
                            PaperProps={{
                                style: {
                                    maxHeight: 48 * 4.5,
                                    width: '20ch',
                                },
                            }}
                        >
                            {options.map((option) => (
                                <MenuItem key={option} selected={option === 'Pyxis'} onClick={this.handleClose}>
                                    <NavItem>
                                        <NavLink href="/login">{option}</NavLink>
                                    </NavItem>
                                </MenuItem>
                            ))}
                        </Menu>
                    </Collapse>
                </Navbar>
            </div>
        );
    }
}

Navbar.propTypes = {
    light: ProtoTypes.bool,
    dark: ProtoTypes.bool,
    fixed: ProtoTypes.string,
    color: ProtoTypes.string,
    role: ProtoTypes.string,
    expand: ProtoTypes.oneOfType([ProtoTypes.bool, ProtoTypes.string]),
    tag: ProtoTypes.oneOfType([ProtoTypes.func, ProtoTypes.string])
}

Nav.propTypes = {
    tabs: ProtoTypes.bool,
    pills: ProtoTypes.bool,
    card: ProtoTypes.bool,
    justified: ProtoTypes.bool,
    fill: ProtoTypes.bool,
    vertical: ProtoTypes.oneOfType([ProtoTypes.bool, ProtoTypes.string]),
    horizontal: ProtoTypes.string,
    navbar: ProtoTypes.bool,
    tag: ProtoTypes.oneOfType([ProtoTypes.func, ProtoTypes.string])
    // pass in custom element to use
}

NavItem.propTypes = {
    tag: ProtoTypes.oneOfType([ProtoTypes.func, ProtoTypes.string]),
    active: ProtoTypes.bool,
    // pass in custom element to use
}

NavLink.propTypes = {
    disabled: ProtoTypes.bool,
    active: ProtoTypes.bool,
    // pass in custom element to use
    tag: ProtoTypes.oneOfType([ProtoTypes.func,ProtoTypes.string]),
    // ref will only get you a reference to the NavLink component, use innerRef to get a reference to the DOM element (for things like focus management).
    innerRef: ProtoTypes.oneOfType([ProtoTypes.func, ProtoTypes.string])
}

NavbarBrand.propTypes = {
    tag: ProtoTypes.oneOfType([ProtoTypes.func, ProtoTypes.string])
    // pass in custom element to use
}


Header.protoTypes = {
    classes: ProtoTypes.object,
}

export default withStyles(styles)(Header);