import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import scanAction from "../../action/scanAction/scanAction";
import * as Api from '../../api/scanApi'


function * getScanAction({payload}) {
    let resp = yield call(Api.getScans,"api/scans");
    const {status, scans} = resp;
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(scanAction.getScanSuccess(scans));
    }else{
        yield put(scanAction.getScanFaild());
    }
}



function* getScanSaga() {
    yield takeLatest(config.GET_SCAN_ACTION,getScanAction)
}

export default getScanSaga;
