import {fork} from "redux-saga/effects";
import scanSaga from './scanSaga/scanSaga'
import signaturesSaga from './signaturesSaga'
import summarySaga from './summarySaga'
import signStatSaga from "./signaturesStatSaga";
import getScandetailSaga from "./scandetailSaga";
import addScanSaga from "./addScanSaga";


function* rootSaga(){
    yield fork(scanSaga)
    yield fork(signaturesSaga)
    yield fork(summarySaga)
    yield fork(signStatSaga)
    yield fork(getScandetailSaga)
    yield fork(addScanSaga)
}

export default rootSaga