import React, {useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import {connect} from "react-redux";
import {bindActionCreators, compose} from "redux";
import scanActions from "../../redux/action/scanAction/scanAction";
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import CircularProgress from '@material-ui/core/CircularProgress';
import scanDetailActions from "../../redux/action/scanDetailAction";
import {useHistory} from 'react-router'
import ShowSignaturesActions from "../../redux/action/signaturesAction";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import {toast} from 'react-toastify';
import addScanAction from '../../redux/action/addScanAction'


const useStyles = makeStyles({
    table: {
        minWidth: 500,
    },
    root: {
        minWidth: 500,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
    paper: {
        width: 200,
        height: 300,
        overflow: 'auto',
    },
    selectEmpty: {
        marginTop: 2,
    },
});

function not(a, b) {
    return a.filter((value) => b.indexOf(value) === -1);
}

function intersection(a, b) {
    return a.filter((value) => b.indexOf(value) !== -1);
}


function AddScan(props) {
    const classes = useStyles();
    useEffect(() => {
        props.showSignaturesActions.getSignatures()
    }, [])

    useEffect(() => {
        setLeft(props.signatures)
    }, [props.signatures])

    useEffect(()=>{
        if(props.isSuccess===true){
            props.addScanAction.resetIsSuccess()
            history.push("/scans")
        }
    },[props.isSuccess])
    const [left, setLeft] = React.useState([]);

    let history = useHistory()
    const [checked, setChecked] = React.useState([]);
    const [right, setRight] = React.useState([]);
    const [concurence, setConcurence] = React.useState(null);
    const [url, setUrl] = React.useState('')
    const handleChange = (event) => {
        setConcurence(event.target.value);
    };

    const leftChecked = intersection(checked, left);
    const rightChecked = intersection(checked, right);

    const handleToggle = (value) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };

    const handleAllRight = () => {
        setRight(right.concat(left));
        setLeft([]);
    };

    const handleCheckedRight = () => {
        setRight(right.concat(leftChecked));
        setLeft(not(left, leftChecked));
        setChecked(not(checked, leftChecked));
    };

    const handleCheckedLeft = () => {
        setLeft(left.concat(rightChecked));
        setRight(not(right, rightChecked));
        setChecked(not(checked, rightChecked));
    };

    const handleAllLeft = () => {
        setLeft(left.concat(right));
        setRight([]);
    };

    const handleUrl = (value) => {
        setUrl(value.target.value)
    }

    const sendForm = () => {
        if (right.length === 0) {
            toast.error("No signature loaded!")
        }
        else if(url===""){
            toast.error("No url!")
        }
        else if(concurence==null){
            toast.error("No concurence!")
        }
        else{
            var signList = []
            right.forEach(item=>{
                signList.push(item.AsbPath)
            })
            props.addScanAction.addScan({
                host:url,
                signatures:signList,
                num:concurence
            })
        }
    }

    const customList = (items) => (
        <Paper className={classes.paper}>
            <List dense component="div" role="list">
                {
                    left ?
                        items.map((value) => {
                            const labelId = `transfer-list-item-${value}-label`;

                            return (
                                <ListItem key={value.id} role="listitem" button onClick={handleToggle(value)}>
                                    <ListItemIcon>
                                        <Checkbox
                                            checked={checked.indexOf(value) !== -1}
                                            tabIndex={-1}
                                            disableRipple
                                            inputProps={{'aria-labelledby': labelId}}
                                        />
                                    </ListItemIcon>
                                    <ListItemText id={labelId} primary={`${value.Name}`}/>
                                </ListItem>
                            );
                        }) : null}
                <ListItem/>
            </List>
        </Paper>
    );

    return (
        <>
            <h1>Add New Record</h1>
            <TableContainer component={Paper} style={{marginTop: "30"}}>
                <Table className={classes.table} aria-label="simple table" size="medium">
                    <TableHead>
                        <TableRow>
                            <TableCell width="40%"></TableCell>
                            <TableCell align="right">
                                <Button onClick={sendForm} variant="contained" color="primary">
                                    Scan
                                </Button>
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell>URL</TableCell>
                            <TableCell align="left">
                                <TextField onChange={handleUrl} id="standard-basic" label="Url"/>
                            </TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Signatures</TableCell>
                            <TableCell align="left">

                                {
                                    props.loading ?
                                        <CircularProgress color="secondary"/>
                                        : <Grid container spacing={2} alignItems="flex-end">
                                            <Grid item>Choices{customList(left)}</Grid>
                                            <Grid item>
                                                <Grid container direction="column" alignItems="flex-center">
                                                    <Button
                                                        variant="outlined"
                                                        size="small"
                                                        className={classes.button}
                                                        onClick={handleAllRight}
                                                        disabled={left ? left.length === 0 : false}
                                                        aria-label="move all right"
                                                    >
                                                        
                                                    </Button>
                                                    <Button
                                                        variant="outlined"
                                                        size="small"
                                                        className={classes.button}
                                                        onClick={handleCheckedRight}
                                                        disabled={leftChecked.length === 0}
                                                        aria-label="move selected right"
                                                    >
                                                        &gt;
                                                    </Button>
                                                    <Button
                                                        variant="outlined"
                                                        size="small"
                                                        className={classes.button}
                                                        onClick={handleCheckedLeft}
                                                        disabled={rightChecked.length === 0}
                                                        aria-label="move selected left"
                                                    >
                                                        &lt;
                                                    </Button>
                                                    <Button
                                                        variant="outlined"
                                                        size="small"
                                                        className={classes.button}
                                                        onClick={handleAllLeft}
                                                        disabled={right.length === 0}
                                                        aria-label="move all left"
                                                    >
                                                        
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                            <Grid item>Chosen{customList(right)}</Grid>
                                        </Grid>
                                }
                            </TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>Concurency</TableCell>
                            <TableCell align="left">
                                <InputLabel id="demo-simple-select-outlined-label">Concurence</InputLabel>
                                <Select
                                    labelId="demo-simple-select-outlined-label"
                                    id="demo-simple-select-outlined"
                                    value={concurence}
                                    onChange={handleChange}
                                    label="Concurence"
                                >
                                    <MenuItem value={10}>10</MenuItem>
                                    <MenuItem value={20}>20</MenuItem>
                                    <MenuItem value={30}>30</MenuItem>
                                </Select>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    );
}


const mapDispatchToProps = dispatch => {
    return {
        showSignaturesActions: bindActionCreators(ShowSignaturesActions, dispatch),
        addScanAction:bindActionCreators(addScanAction,dispatch)
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        signatures: state.SignaturesReducer.data,
        loading: state.SignaturesReducer.loading,
        isSuccess:state.AddScanReducer.isSuccess
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps,
)

export default compose(withConnect)(AddScan);