import React from 'react';
import Dashboard from "./page/Dashboard";
import Login from './page/login'
import Scans from './page/scan';
import Signatures from './page/signature'
import Summary from './page/summary'
import AddScan from "./page/addScan";
import ListScan from "./page/listScan";

const routes = [
    {
        path:"/",
        exact:true,
        main:()=><Dashboard />
    },
    {
        path:"/scans",
        exact: false,
        main:()=><Scans/>
    },
    {
        path:"/signatures",
        exact: false,
        main:()=><Signatures/>
    },
    {
        path:"/summary",
        exact: false,
        main:()=><Summary />
    },
    {
        path:"/login",
        exact: false,
        main:()=><Login/>
    },
    {
        path:"/addScan",
        exact: false,
        main:()=><AddScan />
    },
    {
        path:"/listScan",
        exact:false,
        main:()=><ListScan />

    }
];

export default routes