import {
    GET_SIGNATURE_FAILED,GET_SIGNATURE_SUCCESS,GET_SIGNATURE_ACTION
} from "../../../constant/config";

const ShowSignaturesActions = {
    getSignatures,
    getSignaturesSuccess,
    getSignaturesFaild,
}

function getSignaturesFaild() {
    return{
        type: GET_SIGNATURE_FAILED,
        payload:{

        }
    }
}

function getSignaturesSuccess(signatures) {
    return{
        type: GET_SIGNATURE_SUCCESS,
        data: signatures
    }
}

function getSignatures() {
    return {
        type: GET_SIGNATURE_ACTION,
    }
}



export default ShowSignaturesActions;