import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import scandetailAction from "../../action/scanDetailAction";
import * as Api from '../../api/scandetailAPI'


function * getScandetailAction({payload}) {
    var {id} = payload
    let resp = yield call(Api.getScanDetails,"api/scan/"+id+"/");
    const { status, records} = resp;
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(scandetailAction.getScandetailSuccess(records));
    }else{
        yield put(scandetailAction.getScandetail());
    }
}



function* getScandetailSaga() {
    yield takeLatest(config.GET_SCANDETAIL_ACTION,getScandetailAction)
}

export default getScandetailSaga;