import {
    ADD_SCAN_ACTION,ADD_SCAN_FAILED,ADD_SCAN_SUCCESS,RESET_IS_SUCCESS_ADD_SCAN
} from "../../../constant/config";

const AddScanActions = {
    addScan,
    addScanSuccess,
    addScanFaild,
    resetIsSuccess,
}

function addScanFaild() {
    return{
        type: ADD_SCAN_FAILED
    }
}

function addScanSuccess() {
    return{
        type: ADD_SCAN_SUCCESS,
    }
}

function addScan(scan) {
    return {
        type: ADD_SCAN_ACTION,
        payload:{
            scan:scan
        }
    }
}

function resetIsSuccess(){
    return {
        type: RESET_IS_SUCCESS_ADD_SCAN
    }
}



export default AddScanActions;
