import {
    GET_SIGN_STAT_ACTION,GET_SIGN_STAT_FAILED,GET_SIGN_STAT_SUCCESS
} from "../../../constant/config";

const ShowSignStatActions = {
    getSignStat,
    getSignStatSuccess,
    getSignStatFaild,
}

function getSignStatFaild() {
    return{
        type: GET_SIGN_STAT_FAILED,
        payload:{

        }
    }
}

function getSignStatSuccess(data,categories) {
    return{
        type: GET_SIGN_STAT_SUCCESS,
        data: data,
        categories:categories
    }
}

function getSignStat() {
    return {
        type: GET_SIGN_STAT_ACTION,
    }
}



export default ShowSignStatActions;