import * as config from '../../../constant/config'
import {toast} from 'react-toastify';

const initial = {
    loading:false,
    isSuccess:false
}

const AddScanReducer = (state = initial, action) => {
    switch (action.type) {
        case config.ADD_SCAN_ACTION: {
            return {...state,loading: true}
        }
        case config.ADD_SCAN_SUCCESS:{
            toast.success("Add New Record Success")
            return {...state,loading:false,isSuccess: true}
        }
        case config.ADD_SCAN_FAILED:{
            return {...state,loading: false}
        }
        case config.RESET_IS_SUCCESS_ADD_SCAN:{
            return{...state,isSuccess: false}
        }
        default:
            return state;
    }
}

export default AddScanReducer;