import {
    GET_SUMMARY_ACTION,GET_SUMMARY_FAILED,GET_SUMMARY_SUCCESS
} from "../../../constant/config";

const ShowSummaryActions = {
    getSummary,
    getSummarySuccess,
    getSummaryFaild,
}

function getSummaryFaild() {
    return{
        type: GET_SUMMARY_FAILED,
        payload:{

        }
    }
}

function getSummarySuccess(data) {
    return{
        type: GET_SUMMARY_SUCCESS,
        data: data
    }
}

function getSummary() {
    return {
        type: GET_SUMMARY_ACTION,
    }
}



export default ShowSummaryActions;