import {
    GET_SCANDETAIL_FAILED,GET_SCANDETAIL_SUCCESS,GET_SCANDETAIL_ACTION,SET_SCANID_ACTION
} from "../../../constant/config";

const ShowScandetailActions = {
    getScandetail,
    getScandetailSuccess,
    getScandetailFaild,
    setScanID
}

function getScandetailFaild() {
    return{
        type: GET_SCANDETAIL_FAILED,
        payload:{

        }
    }
}

function getScandetailSuccess(records) {
    return{
        type: GET_SCANDETAIL_SUCCESS,
        data: records
    }
}

function getScandetail(id) {
    return {
        type: GET_SCANDETAIL_ACTION,
        payload:{
            id:id
        }
    }
}

function setScanID(id) {
    return{
        type: SET_SCANID_ACTION,
        payload: {
            id:id
        }
    }
}


export default ShowScandetailActions;