import {combineReducers} from "redux"
import ScanReducer from "./scanReducer";
import SignaturesReducer from './signaturesReducer'
import SummaryReducer from "./summaryReducer";
import SignStatReducer from "./signStatReducer";
import ScandetailReducer from './scandetailReducer'
import AddScanReducer from "./addScanReducer";


const rootReducer = combineReducers({
    ScanReducer: ScanReducer,
    SignaturesReducer:SignaturesReducer,
    SummaryReducer:SummaryReducer,
    SignStatReducer:SignStatReducer,
    ScandetailReducer:ScandetailReducer,
    AddScanReducer:AddScanReducer,
})

export default rootReducer