import * as config from "../../../constant/config";
import {call, delay, put, takeLatest} from "redux-saga/effects";
import STATUS_CODE from "../../../constant/STATUS_CODE";
import signStatAction from "../../action/signStatAction";
import * as Api from '../../api/signStatApi'


function * getSignStatAction({payload}) {
    let resp = yield call(Api.getSignStat,"api/stats/sign");
    const {status,data,categories} = resp
    yield delay(1000);
    if(STATUS_CODE.SUCCESS==status){
        yield put(signStatAction.getSignStatSuccess(data,categories));
    }else{
        yield put(signStatAction.getSignStatFaild());
    }
}



function* getSignStatSaga() {
    yield takeLatest(config.GET_SIGN_STAT_ACTION,getSignStatAction)
}

export default getSignStatSaga;