import * as config from '../../../constant/config'
const initial = {
    loading:false
}

const ScandetailReducer = (state = initial, action) => {
    switch (action.type) {
        case config.GET_SCANDETAIL_ACTION: {
            return {...state,loading: true}
        }
        case config.GET_SCANDETAIL_SUCCESS:{
            var data = action.data
            for(var i = 0;i<data.length;i=i+1){
                var newResRaw = atob(data[i].ResRaw)
                var newReqRaw = atob(data[i].ReqRaw)
                data[i].ReqRaw=newReqRaw
                data[i].ResRaw=newResRaw
            }
            return {...state,loading:false, data:data}
        }
        case config.GET_SCANDETAIL_FAILED:{
            return {...state,loading: false}
        }
        case config.SET_SCANID_ACTION: {
            return{...state, id: action.payload.id}
        }
        default:
            return state;
    }
}

export default ScandetailReducer;