import {
    GET_SCAN_FAILED,GET_SCAN_SUCCESS,GET_SCAN_ACTION
} from "../../../constant/config";

const ShowScanActions = {
    getScan,
    getScanSuccess,
    getScanFaild,
}

function getScanFaild() {
    return{
        type: GET_SCAN_FAILED,
        payload:{

        }
    }
}

function getScanSuccess(scans) {
    return{
        type: GET_SCAN_SUCCESS,
        data: scans
    }
}

function getScan() {
    return {
        type: GET_SCAN_ACTION,
    }
}



export default ShowScanActions;
