import React, {useEffect} from 'react';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import {connect} from "react-redux";
import {bindActionCreators, compose} from "redux";
import scanActions from "../../redux/action/scanAction/scanAction";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import VisibilityIcon from '@material-ui/icons/Visibility';
import CircularProgress from '@material-ui/core/CircularProgress';
import Chip from '@material-ui/core/Chip';
import scanDetailActions from "../../redux/action/scanDetailAction";
import {useHistory} from 'react-router'
import {Link, NavLink} from "react-router-dom";

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: '#D7DBDD',
        color: theme.palette.common.black,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);


const useStyles = makeStyles({
    table: {
        minWidth: 300,
    },
});


function CustomizedTables(props) {
    const classes = useStyles();
    useEffect(() => {
        props.scanActions()
    }, []);

    let history = useHistory()
    const addPage = () => {
        history.push("/addScan")
    }

    const listPage = (id) => {
        props.scanDetailActions.setScanID(id)
        history.push("/listScan")
    }    

    const getScanDetail = (ScanID) => {
        props.scanDetailActions.getScandetail(ScanID)
    }

    return (
        <>
            <h2>Scan Summary</h2>
            <div style={{width: "auto", height: "3px", backgroundColor: "gray"}}></div>
            <div style={{width: "auto", height: "100px", position: "relative"}}>
                <Link to="/addScan" className={classes.activeClassName}>
                    <Fab variant="extended" style={{position: "absolute", top: "25px", right: "10px"}}>
                        <AddIcon/>
                        Add Record
                    </Fab>
                </Link>

            </div>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>Detail</StyledTableCell>
                            <StyledTableCell>Scan ID</StyledTableCell>
                            <StyledTableCell>Mode</StyledTableCell>
                            <StyledTableCell>Create At</StyledTableCell>
                            <StyledTableCell>Signatures</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {props.dataAPI ?
                            props.dataAPI.map((row) => (
                                <StyledTableRow>
                                    <StyledTableCell  style={{ width: "15%" }}>
                                        <Button onClick={()=> listPage(row.ScanID)/**()=>getScanDetail(row.ScanID) */} variant="contained" color="black">
                                            <VisibilityIcon/>
                                            Detail
                                        </Button>
                                    </StyledTableCell>
                                    <StyledTableCell>{row.ScanID}</StyledTableCell>
                                    <StyledTableCell>Scan</StyledTableCell>
                                    <StyledTableCell>{row.created_at}</StyledTableCell>
                                    <StyledTableCell><Chip className={classes.chip} style={{backgroundColor: "#6cec94"}}
                                                           label={row.SignatureID}/></StyledTableCell>
                                </StyledTableRow>
                            )) : null}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                props.loading ?
                    <CircularProgress color="secondary"/>
                    : null
            }
        </>
    );
}


const mapDispatchToProps = dispatch => {
    return {
        scanActions: () => dispatch(scanActions.getScan()),
        scanDetailActions:bindActionCreators(scanDetailActions,dispatch),
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        dataAPI: state.ScanReducer.data,
        loading: state.ScanReducer.loading
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps,
)

export default compose(withConnect)(CustomizedTables);