import React,{useEffect } from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import VisibilityIcon from '@material-ui/icons/Visibility';
import {bindActionCreators, compose} from "redux";
import { connect } from "react-redux";
import SignaturesAction from '../../redux/action/signaturesAction'
import CircularProgress from '@material-ui/core/CircularProgress';
import Chip from '@material-ui/core/Chip';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: '#D7DBDD',
        color: theme.palette.common.black,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);



const useStyles = makeStyles({
    table: {
        minWidth: 300,
    },
});



function CustomizedTables(props) {
    const classes = useStyles();
    useEffect(() => {
        props.signaturesActions()
    }, []);

    return (
        <>
            <h2>Signatures Summary</h2>
            <div style={{width:"auto", height:"3px" ,backgroundColor:"gray"}}></div>
            <div style={{width:"auto", height:"100px", position:"relative"}}>

            </div>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>SignatureID</StyledTableCell>
                            <StyledTableCell>Signature</StyledTableCell>
                            <StyledTableCell>Risk</StyledTableCell>
                            <StyledTableCell>Absolute Path</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {props.dataAPI ?
                            props.dataAPI.map((row) => (
                                <StyledTableRow>
                                    <StyledTableCell><Chip className={classes.chip} style={{backgroundColor:"#6cec94"}} label={row.SignID}/></StyledTableCell>
                                    <StyledTableCell>{row.Name}</StyledTableCell>
                                    <StyledTableCell><Chip className={classes.chip} color="secondary" label={row.Risk}/></StyledTableCell>
                                    <StyledTableCell>{row.AsbPath}</StyledTableCell>
                                </StyledTableRow>
                            )):null}
                    </TableBody>
                </Table>
            </TableContainer>
            {
                props.loading?
                    <CircularProgress color="secondary" />
                    :null
            }
        </>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        signaturesActions: () => dispatch(SignaturesAction.getSignatures()),
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        dataAPI:state.SignaturesReducer.data,
        loading:state.SignaturesReducer.loading
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
)

export default compose(withConnect)(CustomizedTables);