import callApi from "./axios";
import StatusCode from '../../constant/STATUS_CODE'


class axiosService {
    get(endpoint,body){
        return callApi(endpoint,"GET",body).then(res => {
            if(res.status==StatusCode.SUCCESS){
                return res.data
            }
        }).catch(err=>{
            console.log(err.response)
        })
    }


    post(endpoint,body){
        return callApi(endpoint,'POST',JSON.parse(JSON.stringify(body))).then(res=>{
            if(res.status==StatusCode.SUCCESS){
                return res
            }
        }).catch(err=>{
            console.log(err.response)
        })
    }
}

export default new axiosService()