import * as config from '../../../constant/config'

const initial = {
    loading:false
}

const SignStatReducer = (state = initial, action) => {
    switch (action.type) {
        case config.GET_SIGN_STAT_ACTION: {
            return {...state,loading: true}
        }
        case config.GET_SIGN_STAT_SUCCESS:{
            const data = [];
            const categories = [];
            for (let i =0;i<action.data.length;i=i+1){
                data.push(action.data[i])
                categories.push(action.categories[i])
            }
            return {...state,loading:false,data:data,categories:categories}
        }
        case config.GET_SIGN_STAT_FAILED:{
            return {...state,loading: false}
        }
        default:
            return state;
    }
}

export default SignStatReducer;