import React from 'react'
import ReactApexChart from 'react-apexcharts'
import {bindActionCreators, compose} from "redux";
import {connect} from "react-redux";
import signStatAction from "../redux/action/signStatAction";


class ApexChart extends React.Component {
    constructor(props) {
        super(props);

        this.state = {};
    }

    componentDidMount() {
        this.props.signStatAction.getSignStat()
    }




    render() {
        return (
            <div id="chart">
                <ReactApexChart options={{
                    chart: {
                        type: 'bar',
                    },
                    plotOptions: {
                        bar: {
                            borderRadius: 4,
                            horizontal: true,
                        }
                    },
                    dataLabels: {
                        enabled: false
                    },
                    xaxis: {
                        categories: this.props.categories ? this.props.categories : []
                    }

                }} series={[{
                    data: this.props.dataColum ? this.props.dataColum : []
                }]} type="bar" height={620}/>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        signStatAction: bindActionCreators(signStatAction, dispatch)
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        dataColum: state.SignStatReducer.data,
        categories: state.SignStatReducer.categories
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
)


export default compose(withConnect)(ApexChart);